"use client";

import { useState, ChangeEvent, FormEvent } from "react";
import { EyeOff, Eye } from 'lucide-react';
// import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button"
import Image from "next/image";

interface SignInButtonProps {
  type: "button" | "submit" | "reset";
  isLogin: boolean;
}

export default function LoginForm() {
//   const router = useRouter();
  const [email, setEmail] = useState<string>("");
  const [password, setPassword] = useState<string>("");
  const [showPassword, setShowPassword] = useState<boolean>(false);
  const [emailError, setEmailError] = useState<string>("");
  const [passwordError, setPasswordError] = useState<string>("");

  const handleEmailChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setEmail(value);
    if (!validateEmail(value)) {
      setEmailError("Invalid email");
    } else {
      setEmailError("");
    }
  };

  const handlePasswordChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setPassword(value);
    if (!validatePassword(value)) {
      setPasswordError("Password must be at least 6 characters");
    } else {
      setPasswordError("");
    }
  };

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!validateEmail(email)) {
      setEmailError("Invalid email");
      return;
    }
    if (!validatePassword(password)) {
      setPasswordError("Password must be at least 6 characters");
      return;
    }
    console.log("Submitting:", email, password);
    // router.push("/dashboard");
  };

  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  return (
    <div className="space-y-1 w-11/12 md:w-1/2 lg:w-1/3 xl:w-1/4">
      <div className="flex items-center justify-center flex-col">
        <div className="flex items-center justify-center text-2xl">
          <Image src="/images/logo.svg" width={80} height={80} alt="logo" />
        </div>
        <div className="flex items-center justify-center text-2xl font-bold pt-16">
          Login to Gen AI
        </div>
        <div className="w-full pt-11">
          <SignInButton type="button" isLogin={false} />
        </div>
        <div className="flex items-center justify-center w-full pt-11">
          <hr className="w-1/2 border-t border-gray-300 mx-4" />
          <div className="font-normal text-black text-xs text-nowrap w-full flex justify-center">
            or sign in with your email
          </div>
          <hr className="w-1/2 border-t border-gray-300 mx-4" />
        </div>

        <form onSubmit={handleSubmit} className="w-full pt-12">
          <div className="w-full">
            <label
              htmlFor="email"
              className="block text-sm font-medium text-black py-2"
            >
              Email Address
            </label>
            <div className="flex items-center border py-2  border-[#C1C1C1] rounded-lg overflow-hidden mb-4">
              <input
                id="email"
                type="text"
                className={`w-full placeholder-[#00000059] font-normal pl-4 focus:outline-none ${
                  emailError && "border-red-500"
                }`}
                placeholder="Email Address"
                value={email}
                onChange={handleEmailChange}
              />
            </div>
            {emailError && (
              <div className="text-red-500 text-sm">{emailError}</div>
            )}
          </div>
          <div className="w-full">
            <div className="flex items-center justify-between py-2">
              <label
                htmlFor="password"
                className="block text-sm font-medium text-black"
              >
                Password
              </label>
              <a className="text-right text-xs text- cursor-pointer text-[#4A84ED] underline">
                Forgot Password?
              </a>
            </div>
            <div className="flex items-center border py-2  border-[#C1C1C1] rounded-lg overflow-hidden mb-4">
              <input
                id="password"
                type={showPassword ? "text" : "password"}
                className={`w-full placeholder-[#00000059] font-normal pl-4 focus:outline-none ${
                  passwordError && "border-red-500"
                }`}
                placeholder="Password"
                value={password}
                onChange={handlePasswordChange}
              />
              <button
                type="button"
                onClick={togglePasswordVisibility}
                className="px-3 py-1 bg-transparent text-gray-500 focus:outline-none"
              >
                {showPassword ? (
                  <Eye className="h-6 w-6" />
                ) : (
                  <EyeOff className="h-6 w-6" />
                )}
              </button>
            </div>
            {passwordError && (
              <div className="text-red-500 text-sm">{passwordError}</div>
            )}
          </div>
          <div className="w-full pt-11">
            {" "}
            <SignInButton type="submit" isLogin={true} />
          </div>
        </form>
        <div className="flex items-center py-2 pt-16">
          <label className="block text-sm font-normal text-black">
            New to Indium Gen AI?{" "}
            <span className="text-right text-xs font-bold cursor-pointer text-[#4A84ED] underline">
              Register
            </span>
          </label>
        </div>
      </div>
    </div>
  );
}

// Validate email format
const validateEmail = (email: string): boolean => {
  const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return re.test(String(email).toLowerCase());
};

// Validate password length
const validatePassword = (password: string): boolean => {
  return password.length >= 6;
};

function SignInButton({ type, isLogin }: SignInButtonProps) {
  return (
    <Button
      type={type}
      className={`flex items-center justify-center rounded-lg mt-4 w-full text-base h-12 ${
        !isLogin
          ? "bg-[#F5F5F5] border border-[#00000033] text-black font-medium"
          : "bg-gradient-to-br linear to-br from-[#CB5EDC] via-[#278EF1] to-[#47F3D0] text-white font-bold"
      }`}
    >
      {!isLogin ? "Sign In using Microsoft" : "Log In"}
    </Button>
  );
}

